<!--template :       html  
  !!!!!!!!!!!!!!!!!!-->
<template>
  <div class="container">
    <div class="messages">
      <!--  1 .
      key message, authorName 
      date   .    
      my github    -->
      <ChannelMessage
        v-for="message in 1"
        :key="message"
        authorName="Gabriel Lopes"
        :date="getDate"
      >
        Well come to my discord-clone. See this project in
        <a class="link" href="https://github.com/Dongnic"
          >my github</a
        >
      </ChannelMessage>
      <!-- 1 ,  Bot-->
      <ChannelMessage
        isBot
        hasMention
        v-for="message in 1"
        :key="message"
        authorName="Bot"
        :date="getDate"
      >
        <Mention></Mention> Hii, how are you ??
      </ChannelMessage>
      <ChannelMessage>{{userInfo}}</ChannelMessage>
      <!--message  , User 1 
       -->
      <ChannelMessage
        authorName="User 1"
        :date="getDate"
        v-for="message in messagesArray"
        :key="message"
      >
        {{ message }}
      </ChannelMessage>
    </div>
    <!-- text message input tag
    enter    -->
    <div class="input-wrapper">
      <input
        type="text"
        name="message"
        v-model="message"
        placeholder="Type a message here, and press enter."
        id="input-message"
        @keypress.enter="writeMessage(message)"
      />
      <!---->
      <div class="icon">
        <At :size="24" />
      </div>
    </div>
  </div>
</template>

<!--   import-->
<script>
import At from 'vue-material-design-icons/At'
import { io } from 'socket.io-client'
// Components
import ChannelMessage from './channel-message'
import Mention from './channel-mention'

export default {
  name: 'channel-data',
  props: {
    userInfo: Object
  },
  components: {
    At,
    ChannelMessage,
    Mention
  },
  data () {
    return {
      messagesArray: [],
      message: '',
      date: ''
    }
  },
  // Get initial messages from the server (enable the "created()" function to connect with the server)
  created () {
    this.socket = io('http://192.168.0.20:3001/')
    this.socket.on('getInitialMessages', messages => {
      this.messagesArray = messages
    })
  },
  // Sending messages to server, and getting the returned messages
  methods: {
    writeMessage (message) {
      this.socket.emit('sendMessage', message)
      this.message = ''
      this.getReturnedMessage()
    },
    getReturnedMessage () {
      this.socket.on('returnMessage', messages => {
        this.messagesArray = messages
      })
    }
  },
  // Get date
  computed: {
    getDate () {
      return new Date().toString()
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  grid-area: cd;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex-flow: column nowrap;
  background-color: var(--primary);
  flex: 1;
}

.input-wrapper {
  width: 100%;
  padding: 0 16px;
  height: 68px;

  input {
    margin-top: 12px;
    width: 100%;
    height: 44px;
    padding: 0 10px 0 57px;
    border-radius: 5px;
    color: var(--white);
    background-color: var(--chat-input);

    &::placeholder {
      color: var(--grey);
      font-size: 16px;
    }
  }

  .icon {
    color: var(--grey);
    position: relative;
    top: -50%;
    left: 14px;
    transition: 0.2s;
    width: 24px;
  }
}

.messages {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 46px - 68px);
  max-height: calc(100vh - 46px - 68px);
  overflow-y: scroll;

  .channelmessage:first-child {
    margin-top: 0;
  }

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--tertiary);
    border-radius: 4px;
  }

  &::-webkit-scrollbar-track {
    background-color: var(--primary);
  }
}

.link {
  color: var(--grey);
  outline: none;
}
</style>
